import classes from './Search.module.sass'
import BlueButton from "../UI/BlueButton/BlueButton";
import {useState} from "react";

const Search = () => {
    const [active, setActive]=useState('mark')
    const handleClickButton = name =>{
        setActive(name)
    }

    const placeholder =()=>{
        switch (active){
            case 'number':
                return ' '
            case 'name' :
                return ' '
            case 'mark' :
                return ' '
            default:
                break
        }

    }

    return (
        <div className={classes.search}>
    <div className={classes.searchTypeBlock}>
        <button onClick={ () => handleClickButton('number')} className={active === 'number' && classes.active} >  </button>
        <button onClick={ ()=> handleClickButton ('mark')} className={active === 'mark' && classes.active} >  </button>
        <button onClick={()=> handleClickButton ('name')} className={active === 'name' && classes.active}>   </button>
    </div>
        <form className={classes.searchForm}>
            <input placeholder={ placeholder()} type="text"/>
            <BlueButton></BlueButton>
        </form>
        </div>
    )

}

export default Search